<template>
  <div class="home">
    <div id="demo" class="carousel slide" data-ride="carousel">
 
      <!-- 指示符 -->
      <ul class="carousel-indicators">
        <li data-target="#demo" data-slide-to="0" class="active"></li>
        <li data-target="#demo" data-slide-to="1"></li>
        <li data-target="#demo" data-slide-to="2"></li>
        <li data-target="#demo" data-slide-to="3"></li>
        <li data-target="#demo" data-slide-to="4"></li>
        <li data-target="#demo" data-slide-to="5"></li>
      </ul>
    
      <!-- 轮播图片 -->
      <div class="carousel-inner">
        <div :class="index==0 ? 'carousel-item active':'carousel-item'" v-for="(carousel,index) in carouselList" :key="carousel.name">
          <div :class="'jumbotron color'+(index+1)">
            <h1>{{carousel.title}}</h1>
            <br>
            <p class="lead">{{carousel.info}}</p>
            <br>
            <p class="lead">
              <router-link class="btn btn-primary btn-lg" :to="'/'+carousel.type+'/'+carousel.id">查看详情</router-link>
            </p>
          </div>
        </div>
      </div>
    
      <!-- 左右切换按钮 -->
      <a class="carousel-control-prev" href="#demo" data-slide="prev">
        <span class="carousel-control-prev-icon"></span>
      </a>
      <a class="carousel-control-next" href="#demo" data-slide="next">
        <span class="carousel-control-next-icon"></span>
      </a>
    
    </div>
    <br>
    <div class="row">
      <div class="col-lg-4">
        <div v-for="project in projectList" 
              :key="project.id"
              @click="link('/'+project.type+'/'+project.id)">
          <div class="card border-secondary">
            <div class="card-body">
              <h4 class="card-title">{{project.title}}</h4>
              <div class="card-subtitle mb-2 text-muted">{{project.time}}</div>
              <p class="card-text">{{project.info}}</p>
              <img style="width: 100%; display: block;" :src="'/static/images/project'+project.id+'-0.jpg'"/>
            </div>
          </div>
          <br>
        </div>
      </div>
      <div class="col-lg-4">
        <div v-for="blog in blogList" 
              :key="blog.id"
              @click="link('/'+blog.type+'/'+blog.id)">
          <div class="card">
            <div class="card-body">
              <h4 class="card-title">{{blog.title}}</h4>
              <div class="card-subtitle mb-2 text-muted">{{blog.time}}</div>
              <p class="card-text">{{blog.info}}</p>
            </div>
          </div>
          <br>
        </div>
      </div>
      <div class="col-lg-4">
        <div v-for="issue in issueList" 
              :key="issue.id"
              @click="link('/'+issue.type+'/'+issue.id)">
          <div class="card border-success">
            <div class="card-header">笔记</div>
            <div class="card-body">
              <h4 class="card-title">{{issue.title}}</h4>
              <p class="card-text">{{issue.info}}</p>
            </div>
          </div>
          <br>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "home",
  components: {
    HelloWorld
  },
  methods: {
    link: function(to) {
      this.$router.push(to);
    }
  },
  data() {
    return {
      carouselList: [],
      projectList: [],
      blogList: [],
      issueList: []
    };
  },
  created() {
    const myList = this.GLOBAL;
    this.carouselList = myList.filter(l => l.home).reverse();
    this.projectList = myList.filter(l => l.type === "projects").reverse();
    this.blogList = myList.filter(l => l.type === "blogs").reverse();
    this.issueList = myList.filter(l => l.type === "issues").reverse();
  }
};
</script>

<style scoped>
.carousel-inner img {
  width: 100%;
  height: 100%;
}
.jumbotron {
  padding: 5rem 10rem;
  height: 25rem;
}
.color1 {
  background-color: rgba(44, 62, 80, 0.3);
}
.color3 {
  background-color: rgba(149, 165, 166, 0.3);
}
.color5 {
  background-color: rgba(24, 188, 155, 0.3);
}
.color2 {
  background-color: rgba(52, 152, 219, 0.3);
}
.color4 {
  background-color: rgba(243, 157, 18, 0.3);
}
.color6 {
  background-color: rgba(231, 77, 60, 0.3);
}
.carousel-indicators {
  bottom: 40px;
}
</style>



